.leaflet-control-container {
  display: none;
}

.leaflet-control-container ~ .leaflet-control-container {
  display: block;
}

.leaflet-top {
  align-items: center;
  display: flex;
  justify-content: flex-end;
  width: 100%;
}
.leaflet-top > .leaflet-control {
  margin-top: 10px !important;
}
.leaflet-top > :last-child {
  margin-right: 10px;
}
@media only screen and (min-width: 768px) {
  .leaflet-top > .leaflet-control {
    margin-top: 20px !important;
  }
  .leaflet-top > :last-child {
    margin-right: 20px;
  }
}
.leaflet-top .leaflet-control-zoom {
  align-items: center;
  border: 1px solid var(--owc-openkaarten-streetmap--primary-color) !important;
  border-radius: 3px;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  margin-right: 8px;
}
.leaflet-top .leaflet-control-zoom a {
  align-items: center;
  border: none;
  border-radius: 3px;
  color: var(--owc-openkaarten-streetmap--primary-color);
  display: flex;
  height: 48px !important;
  justify-content: center;
  width: 44px !important;
}
.leaflet-top .leaflet-control-zoom a:first-child {
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 0;
  border-left: 1px solid var(--owc-openkaarten-streetmap--primary-color) !important;
  border-top-left-radius: 3px;
  border-top-right-radius: 0;
}
.leaflet-top .leaflet-control-filters,
.leaflet-top .leaflet-control-list-view {
  align-items: center;
  border: 1px solid var(--owc-openkaarten-streetmap--primary-color) !important;
  background-color: #fff;
  border-radius: 3px;
  display: flex;
  justify-content: space-between;
  gap: 8px;
  min-width: 44px;
  min-height: 50px;
  padding: 10px 24px;
  margin-left: 0 !important;
}
.leaflet-top .leaflet-control-filters__control-text,
.leaflet-top .leaflet-control-list-view__control-text {
  color: var(--owc-openkaarten-streetmap--primary-color);
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 130%; /* 26px */
}
.leaflet-top .leaflet-control-filters:hover,
.leaflet-top .leaflet-control-list-view:hover {
  cursor: pointer;
  background-color: rgb(244, 244, 244);
}
.leaflet-top .leaflet-control-filters:focus-visible,
.leaflet-top .leaflet-control-list-view:focus-visible {
  border-width: 3px;
}
.leaflet-top .leaflet-control-filters a,
.leaflet-top .leaflet-control-list-view a {
  width: 100%;
  height: 100%;
}
.leaflet-top .leaflet-control-list-view {
  margin-inline-end: 1rem;
}